import History from "history";
import React from "react";
import { Route, Switch, withRouter } from "react-router-dom";
import { FadeTransition, RouterTheme } from "../components";
import { withLayout } from "../hoc";
import { Aquarelle, Artists, Fineline, Home, Neotraditional } from "../pages";

interface Props {
  location: History.Location;
}

const AnimatedRoutes: React.SFC<Props> = ({ location }) => (
  <FadeTransition pageKey={location.key}>
    <RouterTheme location={location}>
      <Switch location={location}>
        <Route exact={true} path="/" component={Home} />
        <Route
          exact={true}
          path="/neotraditional"
          component={withLayout(Neotraditional)}
        />
        <Route exact={true} path="/fineline" component={withLayout(Fineline)} />
        <Route
          exact={true}
          path="/aquarelle"
          component={withLayout(Aquarelle)}
        />
        <Route
          exact={true}
          path="/tattookuenstler"
          component={withLayout(Artists)}
        />
      </Switch>
    </RouterTheme>
  </FadeTransition>
);

export default withRouter(AnimatedRoutes);



