import React, { Component } from "react";
import { BrowserRouter } from "react-router-dom";
import { AnimatedRoutes } from "./components";

class App extends Component<{}, {}> {
  render() {
    return (
      <BrowserRouter>
        <AnimatedRoutes />
      </BrowserRouter>
    );
  }
}

export default App;



