import React from "react";
import styled from "react-emotion";
import { debounce } from "lodash";
import { Grid, Row, Col } from "react-flexbox-grid";
import { HomeLink } from "../components";

const Heading = styled("h1")`
  font-size: 10rem;
`;

interface State {
  readonly height: number;
}

class Home extends React.Component<{}, State> {
  static getWindowHeight = () =>
    window.innerHeight ||
    document.documentElement.clientHeight ||
    document.body.clientHeight;

  constructor(props: {}) {
    super(props);

    this.state = {
      height: Home.getWindowHeight()
    };

    this.setHeight = debounce(this.setHeight, 200);
  }

  componentDidMount(): void {
    this.setHeight();
    window.addEventListener("resize", this.setHeight);
  }

  componentWillUnmount(): void {
    window.removeEventListener("resize", this.setHeight);
  }

  setHeight = () => {
    this.setState(state => {
      const currentHeight = Home.getWindowHeight();
      if (state.height === currentHeight) {
        return null;
      }
      return {
        height: currentHeight
      };
    });
  };

  render(): React.ReactNode {
    return (
      <Grid fluid>
        <Row
          center="xs"
          middle="xs"
          style={{ height: `${this.state.height}px` }}
        >
          <Col xs>
            <Row center="xs">
              <Col>
                <Heading>INK</Heading>
              </Col>
            </Row>
            <Row center="xs">
              <Col md={3}>
                <HomeLink to="/fineline" colorType="fineline">
                  Fineline
                </HomeLink>
              </Col>
              <Col md={3}>
                <HomeLink to="/neotraditional" colorType="neotraditional">
                  Neotraditional
                </HomeLink>
              </Col>
              <Col md={3}>
                <HomeLink to="/aquarelle" colorType="aquarelle">
                  Aquarelle
                </HomeLink>
              </Col>
            </Row>
          </Col>
        </Row>
      </Grid>
    );
  }
}

export default Home;



