import React from "react";
import { Row, Col } from "react-flexbox-grid";
import {
  ImageData,
  ModalWithImage,
  ClickableImage,
  PageHeading
} from "../components";
import { LayoutProps } from "../hoc/withLayout";

import img3 from "../images/fineline/6507677125d6f270c523e78dbb88976d.jpg";
import img1 from "../images/fineline/7f66278d969b1666990f2364a3e3cb73.jpg";
import img2 from "../images/placeholder/2.jpg";

const images: ImageData[] = [
  {
    alt: "Fineline Tattoo",
    modalContent: {
      title: '"Blaue Rose" - Tilo Hose',
      description:
        "Das Besondere an diesem Tattoo ist die Farbe: Auch wenn das Blau hier sehr prägnant und auffällig ist, so bildet es doch eine wunderschöne Komposition aus schwarzen Linien, blauen Blüten und ..."
    },
    url: img1
  },
  {
    alt: "Fineline Tattoo",
    modalContent: {
      title: '"Blaue Rose" - Tilo Hose',
      description:
        "Das Besondere an diesem Tattoo ist die Farbe: Auch wenn das Blau hier sehr prägnant und auffällig ist, so bildet es doch eine wunderschöne Komposition aus schwarzen Linien, blauen Blüten und ..."
    },
    url: img2
  },
  {
    alt: "Fineline Tattoo",
    modalContent: {
      title: '"Blaue Rose" - Tilo Hose',
      description:
        "Das Besondere an diesem Tattoo ist die Farbe: Auch wenn das Blau hier sehr prägnant und auffällig ist, so bildet es doch eine wunderschöne Komposition aus schwarzen Linien, blauen Blüten und ..."
    },
    url: img3
  }
];

type Props = LayoutProps;

class Fineline extends React.Component<Props> {
  handleClickImage = (
    imagesIndex: number
  ): React.MouseEventHandler<any> => () => {
    this.props.setModalContent(
      <ModalWithImage
        imageData={images[imagesIndex]}
        handleCloseModal={this.props.handleCloseModal}
      />
    );
    this.props.handleOpenModal();
  };

  render(): React.ReactNode {
    return (
      <React.Fragment>
        <Row center="xs">
          <PageHeading>Fineline</PageHeading>
        </Row>
        <Row around="xs">
          {images.map((image, index) => (
            <Col key={index} md={3} xs={5} style={{ paddingTop: "30px" }}>
              <ClickableImage
                src={image.url}
                onClick={this.handleClickImage(index)}
              />
            </Col>
          ))}
        </Row>
      </React.Fragment>
    );
  }
}

export default Fineline;



