import History from "history";
import React from "react";
import styled from "react-emotion";
import { Row, Col } from "react-flexbox-grid";
import { Link, withRouter } from "react-router-dom";
import HeaderLink from "./HeaderLink";

interface Props {
  location: History.Location;
}

const Wrapper = styled("div")`
  margin-top: 50px;
  height: 50px;
`;

const Logo = styled("span")`
  font-size: 50px;
  a {
    text-decoration: none;
  }
  a:link,
  a:visited,
  a:active {
    color: ${p => p.theme.colors.text};
  }
  a:hover {
    color: ${p => p.theme.colors.primary};
  }
`;

const isStartPage = (location: History.Location): boolean =>
  !!location.pathname && location.pathname === "/";

// const isArtistsPage = (location: History.Location): boolean =>
//   !!location.pathname && location.pathname === "/tattookuenstler";

const Header: React.SFC<Props> = ({ location }) =>
  isStartPage(location) ? null : (
    <Wrapper>
      <Row center="xs" middle="xs">
        <Col xs>
          <HeaderLink to={"/neotraditional"}>Neotraditional</HeaderLink>
        </Col>
        <Col xs>
          <HeaderLink to={"/fineline"}>Fineline</HeaderLink>
        </Col>
        <Col xs>
          <Logo>
            <Link to="/">INK</Link>
          </Logo>
        </Col>
        <Col xs>
          <HeaderLink to={"/aquarelle"}>Aquarelle</HeaderLink>
        </Col>
        <Col xs>
          <HeaderLink to={"/tattookuenstler"}>Tattookünstler</HeaderLink>
        </Col>
      </Row>
    </Wrapper>
  );

export default withRouter(Header);



